import router from '../../router';
<template>
  <div>
    <!-- 面包屑导航 -->
    <Breadcrumb msg1='商品管理' msg2='商品列表'></Breadcrumb>
    <!-- 卡片视图 -->
    <el-card class="box-card">
      <!-- 搜索框 -->
      <div class="search">
          <el-input
            placeholder="请输入内容"
            v-model="paramsObj.query"
            class="input-with-select"
          >
            <el-button @click="serchGoods" slot="append" icon="el-icon-search"></el-button>
          </el-input>
          <el-button type="primary" @click='addGoods'>添加商品</el-button>
      </div>
      <!-- 表格 -->
      <el-table border :data="tableData" fit style="width: 100%">
          <el-table-column label="#" type="index" width="50"></el-table-column>
          <el-table-column label="商品名称" prop="goods_name" width="650"></el-table-column>
          <el-table-column label="商品价格(元)" prop="goods_price" width="110"></el-table-column>
          <el-table-column label="商品重量" prop="goods_number" width="80"></el-table-column>
          <el-table-column label="创建时间" prop="add_time" :formatter="timestampToTim" width="160"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                :aa="scope.row"
                size="mini"
                type="primary"
                icon="el-icon-edit"
              ></el-button>
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
              ></el-button>
            </template>
          </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="paramsObj.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      tableData: [],
      total: 0,
      paramsObj: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      /* 分页 */
      currentPage4: 1
    }
  },
  created () {
    this.getGoods()
  },
  methods: {
    serchGoods () {},
    addGoods () {
      this.$router.push('/goods/add')
    },
    // 获取商品列表
    async getGoods () {
      const res = await this.$http.get('/goods', {
        params: this.paramsObj
      })
      this.tableData = res.data.data.goods
      this.total = res.data.data.total
    },
    // 时间格式化
    timestampToTim (row) {
      var time = new Date(row.add_time)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      return `${y}-${m < 10 ? '0' + m : m}-${d < 10 ? '0' + d : d} ${h < 10 ? '0' + h : h}:${mm < 10 ? '0' + mm : mm}:${s < 10 ? '0' + s : s}`
    },
    /* 分页 */
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.paramsObj.pagesize = val
      this.getGoods()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.paramsObj.pagenum = val
      this.getGoods()
    }
  }
}
</script>
<style lang='less'  scoped>
.box-card {
  margin-top: 20px;
  .search {
    display: flex;
    .el-input {
      width: 40%;
      margin-right: 20px;
    }
  }
}
</style>
